import React from "react";

import "./photos.scss";
import PhotoBlock from '../../components/ui/PhotoBlock/PhotoBlock';

const Photos = () => (
  <div className="photos">
        <h1 className="photos__heading">Photos of the Happy Couple!</h1>
        <p className="photos__text">Use our hashtag during the wedding so we have record of any photos you take and post!</p>
        <p className="photos__hashtag">#Placeholder</p>
        <section className="photos__photo-section">
          <PhotoBlock photos={['engagement/kissByLake']} />
        </section>
  </div>
);

export default Photos;